import { Component, Input, OnInit } from '@angular/core';
import { InfoAnim, EnumAnim } from '../../interfaces/animation/info-anim';

@Component({
  selector: 'app-sprite-anim',
  templateUrl: './sprite-anim.component.html',
  styleUrls: ['./sprite-anim.component.scss']
})
export class SpriteAnimComponent implements OnInit {

  @Input() defaultAnim: InfoAnim;
  @Input() heightSprite: number;

  currentAnim: InfoAnim;
  currentFrames: string[];
  currentFrame: string;
  indexFrame: number = 0;
  currentInterval: any;

  constructor() { }

  ngOnInit() {
    this.PlayAnimation(this.defaultAnim);
  }

  ngOnDestroy() {
    this.ClearAnimInterval();
  }

  SetDefaultAnimation(newAnim: InfoAnim) {
    this.defaultAnim = Object.create(newAnim);
  }

  PlayAnimation(newAnim: InfoAnim) {
    this.currentAnim = Object.create(newAnim);
    this.SetCurrentFrames(newAnim);
    this.PlayCurrentAnim();
  }

  SetCurrentFrames(info: InfoAnim) {
    this.currentFrames = [];
    for (let i = 1; i <= info.length; i++) {
      this.currentFrames.push("assets/sprites/" + info.name + "/" + info.name + "_(" + i + ")" + ".png")
    }
  }

  PlayCurrentAnim() {
    this.ClearAnimInterval();
    this.indexFrame = 0;
    this.currentFrame = this.currentFrames[this.indexFrame];
    this.currentInterval = setInterval(() => { this.AnimInterval() }, this.currentAnim.speed);
  }

  AnimInterval() {
    this.indexFrame++;
    if (this.indexFrame >= this.currentAnim.length) {
      switch (this.currentAnim.play) {
        case EnumAnim.loop:
          this.indexFrame = 0;
          break;
        case EnumAnim.oneShoot:
          this.PlayAnimation(this.defaultAnim);
          break;
      }
    }
    this.currentFrame = this.currentFrames[this.indexFrame];
  }

  ClearAnimInterval() {
    if (this.currentInterval)
      clearInterval(this.currentInterval);
  }

}
