import { InfoAnim } from "../interfaces/animation/info-anim";
import { ICharacter } from "../interfaces/character/character";

export class Character implements ICharacter {
    private iddleAnim: InfoAnim;
    private attackAnim: InfoAnim;
    private health: number;
    private coldDown: number;
    private damage: number;
    private currentHealth: number;
    private height: number;

    constructor(iddleAnim: InfoAnim, attackAnim: InfoAnim, health: number, coldDown: number, damage: number, height: number) {
        this.iddleAnim = iddleAnim;
        this.attackAnim = attackAnim;
        this.health = health;
        this.currentHealth = health;
        this.coldDown = coldDown;
        this.damage = damage;
        this.height = height;
    }

    GetIddle(): InfoAnim {
        return this.iddleAnim;
    }

    GetAttack(): InfoAnim {
        return this.attackAnim;
    }

    IsPlayer() {
        return false;
    }

    TakeDamage(damage: number) {
        this.currentHealth -= damage;
        if (this.currentHealth <= 0) {
            this.Death();
        }
    }

    Death() {
        this.currentHealth = 0;
    }

    GetHeight(){
        return this.height;
    }

}