import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { ICharacter } from '../../interfaces/character/character';
import { SpriteAnimComponent } from '../sprite-anim/sprite-anim.component';


@Component({
  selector: 'app-character',
  templateUrl: './character.component.html',
  styleUrls: ['./character.component.scss']
})
export class CharacterComponent implements OnInit {

  @Input() charInfo : ICharacter;
  @Input() isPlayer: boolean = false;

  @ViewChild('mainAnim', {static: false}) mainAnim : SpriteAnimComponent;

  constructor() { }

  ngOnInit() {
    setInterval(() => {
      this.mainAnim.PlayAnimation(this.charInfo.GetAttack());
    }, 5000);
  }

}
